
.tape {
    margin-bottom: 50px;  /* Further increase the vertical space after the tape */
    display: flex;
}

.cell-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cell {
    width: 50px;
    height: 50px;
    border: 1px solid #333;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s;
    cursor: pointer;
}

.cell.active {
    background-color: #e0e0e0;
}

.state-display {
    margin-top: 10px;
    border: 1px solid #333;
    padding: 2px 5px;
    font-size: 0.8em;
}

.transition-table {
    margin-top: 20px;
}

.state-row {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.state-name {
    width: 100px;
    font-weight: bold;
}

.transition {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.label {
    margin-right: 10px;
}

.controls {
    margin-top: 20px;
}

.cell {
    width: 40px; /* Make cells a little thinner */
}

.tape-controls {
    margin-top: 10px;
    display: flex;
    justify-content: center;
}

.cell-container {
    position: relative;
}

.tape-controls {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
}

.tape {
    margin-bottom: 50px;  /* Further increase the vertical space after the tape */
    margin-bottom: 30px;  /* Add more vertical space after the tape */
}

.state-display {
    font-size: 14px;  /* Adjust font size for better visibility */
}
